import React from "react";
import {Link, useNavigate} from "react-router-dom";
import {
    Navbar,
    Collapse,
    Nav,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Dropdown,
    Button, Row, Input,
} from "reactstrap";
import user1 from "../assets/images/users/user4.jpg";
import {publicCategoryService, publicProductService} from "../services/public";
import {useDispatch, useSelector} from "react-redux";
import {clearAccessToken} from "../createReduxStore";
import $ from 'jquery';

const Header = () => {
    const user = useSelector((state) => state.user);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [isOpen, setIsOpen] = React.useState(false);
    const [categoryData, setCategoryData] = React.useState([]);
    const [searchData, setSearchData] = React.useState([]);

    const [dropdownOpen, setDropdownOpen] = React.useState(false);

    const toggle = () => setDropdownOpen((prevState) => !prevState);
    const Handletoggle = () => {
        setIsOpen(!isOpen);
    };
    const showMobilemenu = () => {
        document.getElementById("sidebarArea").classList.toggle("showSidebar");
    };

    React.useEffect(() => {
        publicCategoryService.getAllCategory().then(res => {
            setCategoryData(res.data);
        }).catch(error => {
            console.log(error);
        }).finally(() => {
        });
    }, []);

    function handleLogout() {
        dispatch(clearAccessToken());
    }

    function onChangeSearch() {
        publicProductService.getProductSearch($('#searchBar').val()).then(res => {
            setSearchData(res.data.results);
        }).catch(error => {
            console.log(error);
        }).finally(() => {
        });
    }

    function onKeyDown(e) {
        if (e.key === 'Enter') {
            navigate('/products?search_name='+ $('#searchBar').val());
        }
    }

    return (
        <Navbar color="dark" dark expand="md" className="fix-header">
            <div className="d-flex align-items-center">
                <Link to={'/'}>
                    <div className="d-lg-block d-none me-5 pe-3 text-white">
                        <i className="bi bi-house-door me-2"></i>TRANG CHỦ
                    </div>
                </Link>
                <Button
                    color="primary"
                    className=" d-lg-none"
                    onClick={() => showMobilemenu()}
                >
                    <i className="bi bi-list"></i>
                </Button>
            </div>
            <div className="hstack gap-2">
                <Button
                    color="primary"
                    size="sm"
                    className="d-sm-block d-md-none"
                    onClick={Handletoggle}
                >
                    {isOpen ? (
                        <i className="bi bi-x"></i>
                    ) : (
                        <i className="bi bi-three-dots-vertical"></i>
                    )}
                </Button>
            </div>

            <Collapse navbar isOpen={isOpen}>
                <Nav className="me-auto" navbar>
                    <UncontrolledDropdown inNavbar nav>
                        <DropdownToggle caret nav>
                            Danh mục sản phẩm
                        </DropdownToggle>
                        <DropdownMenu end>
                            {categoryData.map((item, index) => (
                                <Link key={index} to={'/products?category=' + item.id}>
                                    <DropdownItem key={index}>{item.name}</DropdownItem></Link>
                            ))}
                        </DropdownMenu>
                    </UncontrolledDropdown>
                    <UncontrolledDropdown className={'m-0 p-0'}>
                        <DropdownToggle className={'no-hover'}>
                            <div className={'searchDiv'}>
                                <Input bsSize="sm" id={'searchBar'} className={'custom-focus'} placeholder={'Tìm kiếm sản phẩm'} onChange={onChangeSearch} onKeyDown={onKeyDown}/></div>
                        </DropdownToggle>
                        <DropdownMenu>
                            {searchData.map((item, index) => (
                                <Link key={index} to={'/products/' + item.id}>
                                    <DropdownItem key={index}>{item.name}</DropdownItem></Link>
                            ))}
                        </DropdownMenu>
                    </UncontrolledDropdown>

                </Nav>
                {!!user &&
                    <Row className={'justify-content-center text-center'}>
                        <p className={'text-white text-center m-auto'}>Xin chào {user.name || user.username}</p>
                    </Row>
                }
                {!!user ?
                    <Dropdown isOpen={dropdownOpen} toggle={toggle}>
                        <DropdownToggle color="transparent">
                            <img
                                src={user1}
                                alt="profile"
                                className="rounded-circle"
                                width="30"
                            ></img>
                        </DropdownToggle>
                        <DropdownMenu>
                            {/*<DropdownItem header>Xin chào</DropdownItem>*/}
                            <Link to={'/profile'}>
                            <DropdownItem>Thông tin cá nhân</DropdownItem></Link>
                            <Link to={'/orders'}>
                                <DropdownItem>Lịch sử đơn hàng</DropdownItem></Link>
                            <Link to={'/cart'}>
                                <DropdownItem>Giỏ hàng</DropdownItem></Link>
                            <DropdownItem divider/>
                            <Link to={'/login'}>
                            <DropdownItem onClick={handleLogout}>Đăng xuất</DropdownItem></Link>
                        </DropdownMenu>
                    </Dropdown>
                    :
                    <Dropdown isOpen={dropdownOpen} toggle={toggle}>
                        <DropdownToggle color="transparent">
                            <img
                                src={user1}
                                alt="profile"
                                className="rounded-circle"
                                width="30"
                            ></img>
                        </DropdownToggle>
                        <DropdownMenu>
                            <Link to={'/login'}>
                                <DropdownItem>Đăng nhập</DropdownItem>
                            </Link>
                        </DropdownMenu>
                    </Dropdown>
                }
            </Collapse>
        </Navbar>
    );
};

export default Header;
